* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.btn {
  min-width: 100px;
  font-size: 24px;
  padding: 10px;
  background-color: #399938;
  border-radius: 10px;
}

.list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.list li {
  flex-basis: calc((100% - 4 * 10px) / 5);
}

.btn {
  cursor: pointer;
}

.is-hiden {
  display: none;
}